import { Component, ViewChild, AfterViewInit, ElementRef, Renderer2 } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { C8yStepper, gettext, retryWithDelay } from '@c8y/ngx-components';
import { SensorPhoneService } from './sensor-phone.service';
import { map, tap, throttleTime } from 'rxjs/operators';
import { DeviceRegistrationService, DeviceRegistrationStatus, InventoryService } from '@c8y/client';
import { Router } from '@angular/router';
import { defer } from 'rxjs';

@Component({
  selector: 'c8y-sensor-phone',
  templateUrl: 'sensor-phone-modal.component.html'
})
export class SensorPhoneModalComponent implements AfterViewInit {
  @ViewChild(C8yStepper, { static: false })
  stepper: C8yStepper;
  @ViewChild(C8yStepper, { read: ElementRef, static: false })
  container: ElementRef;
  pendingDeviceBootstrap: boolean = true;
  success;
  errorMessage = gettext('Failed to register a smartphone.');
  registrationData = null;
  tenantId;
  instance;
  deviceId: string;
  dashboardLink: string = '';
  constructor(
    private bsModalRef: BsModalRef,
    private sensorPhoneService: SensorPhoneService,
    private renderer: Renderer2,
    private deviceRegistrationService: DeviceRegistrationService,
    private router: Router,
    private inventoryService: InventoryService
  ) {
  }

  ngAfterViewInit() {
    this.stepper.selectionChange
      .pipe(throttleTime(100))
      .subscribe((stepper: Partial<C8yStepper>) => this.onStepperSelectionChange(stepper));
    this.tenantId = this.sensorPhoneService.getTenantName();
    this.instance = this.sensorPhoneService.getHost();
  }

  onStepperSelectionChange(stepper: Partial<C8yStepper>) {
    const PHONE_REGISTRATION_STEP_INDEX = 2;
    if (stepper.selectedIndex === PHONE_REGISTRATION_STEP_INDEX) {
      this.registerPhone();
    }
  }

  async registerPhone() {
    try {
      this.deviceId = this.generateDeviceId();
      this.registrationData = this.sensorPhoneService.generateRegistrationData(this.deviceId);
      await this.sensorPhoneService.createPhoneRegistrationRequest(this.deviceId);
      await this.waitForPhoneToConnect();
      await this.sensorPhoneService.acceptPhoneReqistrationRequest(this.deviceId);
      const { deviceMo, dashboardMo } = await this.waitForDeviceAndDashboardMos();
      this.onDeviceAndDashboardMosCreated(deviceMo, dashboardMo);
    } catch (error) {
      this.onFailure();
    }
  }

  waitForPhoneToConnect() {
    return defer(() => this.deviceRegistrationService.detail(this.deviceId))
      .pipe(
        map(res => res.data),
        tap(phoneRegistration => {
          if (phoneRegistration.status === DeviceRegistrationStatus.WAITING_FOR_CONNECTION) {
            throw new Error('Phone is not connected yet.');
          } else {
            this.pendingDeviceBootstrap = false;
          }
        }),
        retryWithDelay(6000, 4)
      ).toPromise();
  }

  waitForDeviceAndDashboardMos() {
    return defer(() => this.inventoryService.list({ owner: `device_${this.deviceId}` }))
      .pipe(
        map(res => res.data),
        map(mos => {
          const deviceMo = mos.find(mo => !!mo.c8y_IsDevice);
          const dashboardMo = mos.find(mo => !!mo.c8y_Dashboard);
          if (!(deviceMo && dashboardMo)) {
            throw new Error('Both device and dashboard MO must be present.');
          } else {
            return { deviceMo, dashboardMo };
          }
        }),
        retryWithDelay(6000, 4)
      ).toPromise();
  }

  onDeviceAndDashboardMosCreated(deviceMo, dashboardMo) {
    this.dashboardLink = `/device/${deviceMo.id}/dashboard/${dashboardMo.id}`;
    this.stepper.next();
    if (this.container) {
      this.renderer.setStyle(this.container.nativeElement, 'display', 'none');
      this.success = true;
    }
  }

  generateDeviceId() {
    return 'phone' + Math.floor(Math.random() * 100000);
  }

  openDashboard() {
    if (this.dashboardLink) {
      this.router.navigate([this.dashboardLink]);
      this.cancel();
    }
  }

  cancel() {
    this.bsModalRef.hide();
    this.deleteDeviceRequest();
  }

  retry() {
    this.success = undefined;
    if (this.container) {
      this.renderer.setStyle(this.container.nativeElement, 'display', 'block');
    }
    this.deleteDeviceRequest();
    this.stepper.selectedIndex = 2;
  }

  onFailure() {
    this.success = false;
    this.stepper.next();
    if (this.container) {
      this.renderer.setStyle(this.container.nativeElement, 'display', 'none');
    }
  }

  async deleteDeviceRequest() {
    if (this.deviceId) {
      try {
        await this.deviceRegistrationService.delete(this.deviceId);
      } catch (error) {
        //
      }
    }
  }
}
