import { Injectable } from '@angular/core';
import { DocLink, ExtensionFactory, gettext, OptionsService } from '@c8y/ngx-components';
import { BsModalService } from 'ngx-bootstrap/modal';
import { of } from 'rxjs';
import { SensorPhoneModalComponent } from './sensor-phone-modal.component';

@Injectable()
export class ConnectSmartphoneFactory implements ExtensionFactory<DocLink> {
  constructor(
    private optionsService: OptionsService,
    private bsModalService: BsModalService
  ) {}
  get() {
    if (this.optionsService.get('sensorPhone', false)) {
      return of([
        {
          icon: 'c8y-icon c8y-icon-mobile-add',
          type: 'quicklink',
          label: gettext('Connect smartphone'),
          url: undefined,
          click: () => {
            this.bsModalService.show(SensorPhoneModalComponent, {
              backdrop: 'static',
              class: 'lg'
            });
          }
        } as DocLink
      ]);
    }
  }
}
