export class RealTimeMock {
  deviceId;
  bootstrapCallback;
  managedObjCallback;
  subscribe(channel, callback) {
    if (channel === '/bootstrap') {
      this.bootstrapCallback = callback;
    } else {
      this.managedObjCallback = callback;
    }
  }
  unsubscribe() {
    /* empty */
  }

  triggerBootstrap(res) {
    this.bootstrapCallback({ data: res });
  }
  triggerManagedObject(res) {
    this.managedObjCallback({ data: res });
  }
}
